import React from 'react';

const CustomerReview = ({review, date}) => {
    return (
        <div className="customer-review">
            <div style={{ margin: 0, position: 'relative', top: '50%', transform: 'translateY(-50%)'}}>
                <p style={{fontStyle: 'italic', fontWeight: 600}}>"{review}"</p>
                <p>{date}</p>
            </div>
        </div>
    )
}

export default CustomerReview;