import React, {useEffect} from 'react';
import MenuBar from './MenuBar/MenuBar';
import Footer from './Footer';

const Main = (props) => {

    useEffect(() => {
        window.scrollTo(0, 0);
    })

    return (
        <React.Fragment>
            <MenuBar />
            {props.children}
            <Footer />
        </React.Fragment>
    )
}

export default Main;