import React, {useState} from 'react';
import ImageCarousel from '../Components/ImageCarousel';

const GridItem = ({gridNumber, images, description}) => {
    const [imageCarousel, setImageCarousel] = useState(false);
    
    return (
        <React.Fragment>
            <div id={`project-grid-${gridNumber}`} className="project-grid-item" style={{backgroundImage: `url(${images[0]})`}}>
            <ion-icon onClick={() => setImageCarousel(true)} name="images-outline"></ion-icon>
            </div>
            <dialog style={{display: imageCarousel ? 'block' : 'none'}} className="grid-dialog">
                <div style={{backgroundColor: 'white', padding: 10, width: '100%', height: '100%'}}>
                <div style={{textAlign: 'right'}} onClick={() => setImageCarousel(false)}><ion-icon name="close-outline"></ion-icon></div>
                    <ImageCarousel images={images} />
                    <div><p style={{textTransform: 'none'}}>{description}</p></div>
                </div>
            </dialog>
        </React.Fragment>
    )
}

export default GridItem;